import { RpJsonDocument } from "@rp/react/rpjson/types"

type UseCollectionViewerInfoProps = {
  rpjsonDocument: RpJsonDocument | undefined
}

type UseCollectionViewerInfoState = {
  shouldUseQuartrViewer: boolean
  collectionId: string | undefined
}

const useShouldUseCollectionViewer = ({
  rpjsonDocument,
}: UseCollectionViewerInfoProps): UseCollectionViewerInfoState => {
  const collectionId = rpjsonDocument?.document.metadata.collection?.rp_collection_id

  const isQuartrDocument = rpjsonDocument?.document.source.rp_provider_id === "QRTR"

  const shouldUseQuartrViewer = isQuartrDocument && !!collectionId

  return { shouldUseQuartrViewer, collectionId }
}

export { useShouldUseCollectionViewer }
export type { UseCollectionViewerInfoProps, UseCollectionViewerInfoState }
